import React from 'react';
import About from '../About/About';
import illustration from '../../images/services/icones-articles/illustration.jpg';
import model1 from '../../images/services/icones-articles/mode-1.jpg';
import model2 from '../../images/services/icones-articles/poon.jpg';


export default function Illustrations() {
    const Text = () => {
        return (
            <div className="text">
                <p>Je vous propose trois options de domaine selon vos projets de
                cration :</p>
                <span><b>Storyboard sur mesure et thme<br />
                    Character design de personnage :<br />
                    Arts traditionnelle/Numrique : </b> Peinture numrique, Peinture
                    acrylique, Feutre alcoolique
                </span><br />
                <b>Paysage / Architecture </b>
                <p>Materiels :</p>
                <p>Crayons, Fusain, Acrylique, Crayons de couleurs</p>
            </div>
        )
    }
    return (

        <div>
            <section id="illustration">
                <div className="container-main">
                    <About
                        img={illustration}
                        paddingTop="120px"
                        paddingBottom="50px"
                        sizeTitle="55px"
                        sizeTxt="25px"
                        title="Illustration :"
                        txt={<Text />} />

                    <article id="styles-design">
                        <div className="flex">
                            <img src={model1} alt="" className="img-illustration" />
                            <aside className="text-style-design purple-color">
                                <h2 className="title">Styles Design </h2>
                                <p>Lhypothse de mon style se dfini par les passions dunivers
                                Fantasy et de magic ainsi que de crature mystrieuse les
                                chimres, les mutations, les fes, les sorcires. Les comptes de
                            lgende minspire pour les histoires et le style quils resso</p><br />
                                <p>Dautre passion me permet dexprimer la crativit par exemple la
                                mode en gnrale inspire le faite de crer des styles diffrents
                            d'habits et d'accessoire.</p><br />
                                <p>Les paysages et larchitecture et pour moi une opportunit de
                                lancer cette crativit dans les formes en perspective et la nature
morte en couleur. </p>
                            </aside>
                            <img src={model2} alt="" className="img-illustration" />
                        </div>
                    </article>
                </div>
            </section>


        </div>
    )
}
